@import url(https://fonts.googleapis.com/css?family=Montserrat:200,300,400,700,900&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/******* GENERAL *******/

:root {
  --turquoise: #52989C;
  --fontColor: #878787;
  --footerBorderOrange: #F76B1C;
  --footerBorderYellow: #FAD961;
}

* {
  font-family: 'Montserrat', sans-serif;
}

html {
  color: #878787;
  color: var(--fontColor);
}

a {
  text-decoration: none;
}


/******* NAV *******/
.nav {
  display: flex;
  justify-content: space-around;
  width: 90%;
  margin: 10px auto 0;
}

.nav-btn {
  border: 1px solid #52989C;
  border: 1px solid var(--turquoise);
  background: #FFF;
  border-radius: 8px;
  padding: 6px 10px;
  font-size: 16px;
  color: #52989C;
  color: var(--turquoise);
}

.nav-btn.active {
  background: #52989C;
  background: var(--turquoise);
  color: #FFF;
  font-weight: 600;
}



/******* MAIN *******/
.header {
  margin: 20px 6px;
}
.header-title {
  font-size: 20px;
  margin: 0 auto;
  text-align: center;
  font-weight: 300;
}

.app:after {
  content: "";
  background-image: url(/art-dent-age/static/media/logo.9a2ec625.jpg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.10;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}

.app {
  width: 100%;
}

.content {
  margin: 10px;
}


.content .content-title {
  font-size: 18px;
  font-weight: 400;
}

.content-title .date {
  font-weight: 600;
}

.flatpickr-input {
  font-size: 20px;
  -webkit-appearance: none;
  border: 1px solid #52989C;
  border: 1px solid var(--turquoise);
  color: #52989C;
  color: var(--turquoise);
  font-weight: 600;
  border-radius: 6px;
  padding: 6px 10px;
}

/* AGE */
.age {
  border: 1px solid #52989C;
  border: 1px solid var(--turquoise);
  width: 150px;
  margin: 50px auto 10px;
  padding: 30px 0;
  background: #52989C;
  background: var(--turquoise);
  border-radius: 16px;
  color: #FFF;
  display: flex;
  justify-content: center;
  position: relative;
}

.age .age-number {
  font-size: 40px;
  text-align: center;
}

.age .age-text {
  position: absolute;
  bottom: 5px;
  font-weight: 200;
}


/******* FOOTER *******/
html, body, #root, .app {
  height: 100%;
}
.app {
  display: flex;
  flex-direction: column;
}
.content {
  flex: 1 0 auto;
}
.footer {
  flex-shrink: 0;
}

.footer {
  text-align: center;
  font-size: 1rem;
  font-weight: 100;
  margin: 0 5px 5px;
  color: #BBB;
}

.footer a {
  display: inline-block;
  padding: 10px 10px 10px 0;
  color: #BBB;
}

.footer .footer-border {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  height: 1px;
  background: -webkit-gradient(linear, right top, left top, from(#F76B1C), to(#FAD961));
  background: -webkit-linear-gradient(right, #F76B1C, #FAD961);
  background: linear-gradient(to left, #F76B1C, #FAD961);
  background: -webkit-gradient(linear, right top, left top, from(var(--footerBorderOrange)), to(var(--footerBorderYellow)));
  background: -webkit-linear-gradient(right, var(--footerBorderOrange), var(--footerBorderYellow));
  background: linear-gradient(to left, var(--footerBorderOrange), var(--footerBorderYellow));
}
